import React, {useState} from 'react'
import AnnounceItem from './AnnonceItem'
import { Link } from 'react-router-dom'
const AnnounceList = ({announcement, handleChoosedAnnounce, handleDeletAnnounce, handleEditAnnouncement, handleSimilarAnnouncement, resetOpenedSimilarAnnouncement}) => {
    const [serchInputValue, setSerchInputValue] = useState('')
    
    const handleSerch = (event) => {
        setSerchInputValue(event.target.value)
    }
    
    return (
        <>
            <div className='headline'>Announcement List</div>
            <div className='nav-conteiner'>
                <input onChange={handleSerch} className='serchInput'  placeholder='Enter serch value'></input>
                <Link className='home-link' to='/' onClick={resetOpenedSimilarAnnouncement}>Home</Link>
            </div>
            <ul className='annonce-list-conteiner'>
                {serchInputValue === '' 
                    ? 
                        announcement.map((announce) => <AnnounceItem key={announce.id} announce={announce} similarAnnouncement={() => handleSimilarAnnouncement(announce)} editAnnouncement={() => handleEditAnnouncement(announce)} deletedAnnounce ={() => handleDeletAnnounce(announce)} choosedAnnounce={() => handleChoosedAnnounce(announce)} /> )
                    :
                        announcement.filter((announce) => announce.title.includes(serchInputValue)).map((announce) => <AnnounceItem key={announce.id} announce={announce} similarAnnouncement={() => handleSimilarAnnouncement(announce)} editAnnouncement={() => handleEditAnnouncement(announce)} deletedAnnounce ={() => handleDeletAnnounce(announce)} choosedAnnounce={() => handleChoosedAnnounce(announce)} /> )
                }    
            </ul>
        </>
    )
}

export default AnnounceList