import React, { useState } from 'react'
import './App.css';
import {Route, BrowserRouter, Switch} from 'react-router-dom'
import Home from './components/Home';
import AnnounceList from './components/AnnounceList';
import defaultAnnouncements from './constans'
import AddNewAnnouncement from './components/AddNewAnnouncement';
import EditAnnouncement from './components/EditAnnouncement';

function App() {
  const [announcement, setAnnouncement] = useState(defaultAnnouncements)
  const [selected, setSelected] = useState(false)
  const [idCounter, setIdCounter] = useState('6')
  

  const getDataOfAnnouncement = () => {
    const date = new Date ()
    const monthInShortStr = date.toLocaleDateString("en-US", { month: 'short' })
    const dateOfAnnouncement = `${monthInShortStr} ${date.getDate()}`
    return dateOfAnnouncement
  }

  const handleEditAnnouncement = (announce) => {
    announce.edit = true
    resetOpenedSimilarAnnouncement()
  }

  const handleConfirmEdit = () => {
    const editingAnnounce = announcement.find((announce) => announce.edit === true)
    
    editingAnnounce.title = document.querySelector('.form-title-input').value
    editingAnnounce.description = document.querySelector('.form-description-input').value
    editingAnnounce.edit = false
    editingAnnounce.date = getDataOfAnnouncement()
  }

  const handleCancleEdit = () => {
    const editingAnnounce = announcement.find((announce) => announce.edit)
    editingAnnounce.edit = false
  }

  const handleSimilarAnnouncement = (announce) => {
    resetOpenedSimilarAnnouncement()
    const similarAnnouncement = announcement.filter((item) => item.description.includes(announce.title) && announce.title !== item.title)
    similarAnnouncement.length !== 0 ? announce.similar = similarAnnouncement : announce.similar = [{title: 'No simular Announcement found'}] 
    announce.openSimilar = true

    setAnnouncement([...announcement])
  }

  const resetOpenedSimilarAnnouncement = () => {
    const prevOpenedSimilar = announcement.find((item) => item.openSimilar === true)
   
    if (prevOpenedSimilar !== undefined) {
      prevOpenedSimilar.openSimilar = false
    }
  }

  const handleChoosedAnnounce = (announce) => {
    if (selected === true) {
      const prevSelectedAnnounce = announcement.find((item) => item.choosed === true)
    
      prevSelectedAnnounce.choosed = false
      setSelected(false)
    }
    announce.choosed = !announce.choosed
    resetOpenedSimilarAnnouncement()

    setSelected(true)
    setAnnouncement([...announcement])
  }

  const handleDeletAnnounce = (deletedAnnounce) => {
    setAnnouncement(announcement.filter(announce => announce.id !== deletedAnnounce.id))
    setSelected(false)
  }

  const handleAddNewAnnouncement = () => {
    const newAnnounceTitle = document.querySelector('.form-title-input').value
    const newAnnounceDescription = document.querySelector('.form-description-input').value

    if (newAnnounceTitle === '' || newAnnounceDescription === '') return
    
    setAnnouncement([...announcement, {title: newAnnounceTitle, description: newAnnounceDescription, choosed: false, date: getDataOfAnnouncement(), id: idCounter + 1, edit: false, openSimilar: false}])
    setIdCounter(idCounter + 1)
  }

  return (
    <BrowserRouter>
      <Switch>
        <Route path='/editing' exect component={() => <EditAnnouncement announcement={announcement} handleConfirmEdit={handleConfirmEdit} handleCancleEdit={handleCancleEdit}/>}/>
        <Route path='/New-Announcement' exect component={() => <AddNewAnnouncement handleAddNewAnnouncement={handleAddNewAnnouncement}/>}/>
        <Route path='/Announce-List' exect component={() => <AnnounceList handleSimilarAnnouncement={handleSimilarAnnouncement} resetOpenedSimilarAnnouncement={resetOpenedSimilarAnnouncement} handleEditAnnouncement={handleEditAnnouncement} handleDeletAnnounce={handleDeletAnnounce} handleChoosedAnnounce={handleChoosedAnnounce} announcement={announcement} />} />
        <Route path='/' exect component={() => <Home/>}/>
      </Switch>
    </BrowserRouter>
  );
}

export default App;
