import React from 'react'
import { Link } from 'react-router-dom'
import { nanoid } from 'nanoid'

const AnnounceItem = ({announce, choosedAnnounce, deletedAnnounce, editAnnouncement, similarAnnouncement}) => {
    return (
        <>
            {announce.choosed === false 
                ?
                    <li onClick={choosedAnnounce} className='annonce-list-item'> 
                        <div className='title'>{announce.title}</div>
                    </li>
                :
                    <li className='annonce-list'> 
                        <div className='annonce-title'>{announce.title}</div> 
                        <div className='annonce-description'>{announce.description}</div>
                        <div className='annonce-footer'>    
                            <button className='btn' onClick={deletedAnnounce}>Delet</button>
                            <div className='annonce-date'>{announce.date}</div>
                            <Link className='btn-edit-link' to='/editing' onClick={editAnnouncement}>Edit </Link>
                        </div>
                        {announce.openSimilar === false 
                            ? (
                                <div className='simular-announce-header' onClick={similarAnnouncement}>Simular Announcement</div>
                              ) 
                            : (
                                <div className='simular-anounce-conteiner'>
                                    {announce.similar.map((item) => <div key={nanoid(10)} className='simular-anounce'> {item.title} </div>)}
                                </div>
                            )
                        }    
                    </li>
            }
            
           
        </>
    )
}

export default AnnounceItem;