import React, { useEffect } from 'react'
import { Link } from 'react-router-dom'

const EditAnnouncement = ({announcement, handleConfirmEdit, handleCancleEdit}) => {
    
    
    useEffect(() => {
        const editingAnounce = announcement.find((announce) => announce.edit === true)
        const inputTitle = document.querySelector('.form-title-input')
        const inputDescription = document.querySelector('.form-description-input')
        
        if (editingAnounce !== undefined) {
            inputTitle.value = editingAnounce.title
            inputDescription.value = editingAnounce.description
        } else {
            inputTitle.value = ''
            inputDescription.value = ''
        }
    }, [])

    return (
        <>
            <div className='headline'>Edit Announcement</div>
            <div className='form-container'>
                <input className='form-title-input' type='text'  placeholder='Title'></input>
                <textarea className='form-description-input'  placeholder='Description'></textarea>
                <div className='form-btn-container'>
                    <Link className='form-btn' onClick={handleConfirmEdit} to='/Announce-List'>Confirm</Link>
                    <Link className='form-btn' onClick={handleCancleEdit} to='/'>Cancel</Link>
                </div>
            </div>
        </>
    )
}

export default EditAnnouncement